@import url(https://fonts.googleapis.com/css?family=Knewave);
@font-face {
  font-family: digital-7;
  src: url(/ReactCalc/static/media/digital-7.1e670d88.ttf); }

body {
  font-family: "digital-7", sans-serif;
  background-color: #e6e6e6;
  font-size: 2.5em !important; }

#calculator {
  border: silver;
  border-radius: 0.25rem;
  box-shadow: 20px 20px 50px grey; }

.grid-container {
  width: 50vw;
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 10px;
  background-color: whitesmoke;
  padding: 10px; }

.cent {
  position: absolute !important;
  left: 50% !important;
  top: 50% !important;
  -webkit-transform: translate(-50%, -50%) !important;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

.grid-container > div {
  text-align: right !important;
  padding-right: 5px !important;
  color: whitesmoke; }

.grid-container > div, .grid-container > button {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  max-height: 100vh; }

.item1x1 {
  font-family: "digital-7" !important;
  font-size: 1.5em !important;
  color: whitesmoke;
  grid-column: 1 / 5;
  height: 80px !important;
  line-height: 40px !important;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  color: #5bc0de;
  text-stroke-width: 1px;
  text-stroke-color: gray;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: gray;
  margin-bottom: -11px; }

.item1x2 {
  font-family: "digital-7" !important;
  font-size: 1.5em !important;
  color: whitesmoke;
  grid-column: 1 / 5;
  height: 80px !important;
  line-height: 40px !important;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  color: #5bc0de;
  text-stroke-width: 1px;
  text-stroke-color: gray;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: gray;
  margin-bottom: 10px; }

.item2 {
  grid-column: 1 / 3;
  grid-row: 3; }

.item3 {
  grid-row: 6 / 8;
  grid-column: 4; }

.item4 {
  grid-column: 1 / 3; }

.metal.linear {
  border-radius: .5em;
  background-image: -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 6%, rgba(255, 255, 255, 0.1) 7.5%), -webkit-repeating-linear-gradient(left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 4%, rgba(0, 0, 0, 0.03) 4.5%), -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 1.2%, rgba(255, 255, 255, 0.15) 2.2%), -webkit-gradient(linear, left top, left bottom, from(#c7c7c7), color-stop(47%, #e6e6e6), color-stop(53%, #c7c7c7), to(#b3b3b3));
  background-image: -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 6%, rgba(255, 255, 255, 0.1) 7.5%), -webkit-repeating-linear-gradient(left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 4%, rgba(0, 0, 0, 0.03) 4.5%), -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 1.2%, rgba(255, 255, 255, 0.15) 2.2%), -webkit-linear-gradient(top, #c7c7c7 0%, #e6e6e6 47%, #c7c7c7 53%, #b3b3b3 100%);
  background-image: -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 6%, rgba(255, 255, 255, 0.1) 7.5%), -webkit-repeating-linear-gradient(left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 4%, rgba(0, 0, 0, 0.03) 4.5%), -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 1.2%, rgba(255, 255, 255, 0.15) 2.2%), linear-gradient(180deg, #c7c7c7 0%, #e6e6e6 47%, #c7c7c7 53%, #b3b3b3 100%); }

.metal {
  cursor: pointer;
  position: relative;
  outline: none;
  text-align: center;
  color: #333333;
  text-shadow: rgba(102, 102, 102, 0.5) 0 -1px 0, rgba(255, 255, 255, 0.6) 0 2px 1px;
  background-color: #e6e6e6;
  box-shadow: inset #262626 0 0px 0px 4px, inset rgba(38, 38, 38, 0.8) 0 -1px 5px 4px, inset rgba(0, 0, 0, 0.25) 0 -1px 0px 7px, inset rgba(255, 255, 255, 0.7) 0 2px 1px 7px, rgba(0, 0, 0, 0.15) 0 -5px 6px 4px, rgba(255, 255, 255, 0.5) 0 5px 6px 4px;
  -webkit-transition: color .2s;
  transition: color .2s; }

.drum-pad {
  background-color: #f0ad4e !important; }

